@import url('https://fonts.googleapis.com/css?family=Baloo+2:400,500,600,700,800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&family=Baloo+Bhaina+2:wght@400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');


$main-color: #004551;
$color2: #0ca890;
$background-color2: #f2f2f2;
$gradient: linear-gradient(to right, $main-color, $color2);
$fontColor: #222222;
$fontColor2: #474747;


//elements
.App {
  color: $fontColor;
//  font-family: 'Baloo 2', sans-serif;
  font-family: 'Baloo Bhaina 2', sans-serif;
  line-height: 125%;
}

.page {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  padding-bottom: 50px;
}

.page-title {
  margin-left: auto;
  margin-right: auto;
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  box-sizing: border-box;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  text-align: center;
  padding: 20px;
  font-size: 2.5em;
  margin: 0px;
}

a {
  text-decoration: none;
  all: inherit;
  cursor: pointer;

  &:active {
    transform: scale(.95)
  }
}

.mobile {
  display: none;
}

//Navbar
.navbar {
  position: absolute;
  width: 100%;
  height: 80px;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}

//Header image
.image-header {
  display: flex;
  align-items: center;
  justify-content: start;
  background: transparent;
  width: 200px;
  padding: 0px;
  color: white;
  margin-left: -10px;

  &:hover {
    cursor: pointer;
  }

  &:hover .mike-image {
    filter: brightness(.7);
  }

  &:hover .name {
    filter: brightness(.7);
  }
}

.name {
  text-align: left;
  font-size: 12px;
  font-weight: 700;
}

.mike-image {
  width: 70px;
  height: 70px;
  clip-path: circle(35%);
}

//Contact button
.contact-label-container {
  height: 100%;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
}

.contact-label {
  width: 80px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0px solid transparent;
  border-radius: 20px;
  font-weight: 600;
  color: $color2;
  background-color: white;
  transition: color .2s, background-color .2s, border .1s ease-out;
  right: 20px;

  &:hover{
    background: $color2;
    color: white;
    border: 2px solid white;
    // box-shadow: 0 0 2px black;
    cursor: pointer;
  }
}

//title page
#title-page {
  min-height: 100vh;
  padding-bottom: 0px;
}

.title-bg-wrapper {
  height: 60vh;
  width: 100%;
  position: absolute;
  z-index: -1;
}

.title-bg {
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  bottom: 0;
  z-index: -1;
}


.title-fade {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  $title-fade-duration: fadein 2s forwards;
  -webkit-animation: $title-fade-duration;
  -moz-animation: $title-fade-duration;
  -ms-animation: $title-fade-duration;
  animation: $title-fade-duration;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
  }

@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
  
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

//test
.skew-container {
  position: relative;
//  background: $main-color;
  height: 50vh;
  width: 100vw
}

.skewed {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, $main-color, $color2);
  z-index: 0;
  transform: skewY(6deg);
  transform-origin: top right;
}

.mybg-subheader {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px 40px 20px;
  margin-top: 5%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.mybg-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.mybg-card {
  width: 20%;
  min-width: 200px;
  padding: 10px;
  text-align: center;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.16);
}

.mybg-title {
  font-weight: 700;
  font-size: 25px;
  padding-top: 20px;
}

.mybg-icon-container {
  width: 120px;
  height: 120px;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.mybg-icon {
  width: 70%;
  height: 70%;
  filter: invert(100%) brightness(.05) sepia(20) saturate(80) hue-rotate(540deg);
}

.mybg-text {
  font-size: 12px;
}

@media only screen and (max-width: 1000px) {
  .mybg-card {
    width: 40%;
  }
}

@media only screen and (max-width: 600px) {
  .mybg-subheader {
    width: 100%;
  }

  .mybg-card {
    width: 100%;
    box-shadow: none;
  }
}

//Projects
#projects {
  background-color: $background-color2;
  text-align: left;
  padding-bottom: 40px
}

.project {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 50px;
  padding-bottom: 50px;
}

.project2 {
  display: flex;
  flex-wrap: wrap-reverse;
}

.project-description-box {
  width: 100%;
  max-width: 400px;
}

.project-title{
  font-weight: 700;
  font-size: 25px;
}

.project-subheader {
  font-weight: 200;
  font-size: 12px;
}

.project-description {
  padding-top: 5px;
  padding-bottom: 10px;
  color: $fontColor2;
}


.icon-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

#project-icon {
  width: 45px;
  height: 45px;
  padding: 5px;
  box-sizing: border-box;
  margin-right: 5px;
  border-radius: 10px;
  background-color: white
}

.description-link-wrapper {
  display: flex;
  width: 100%;
  margin-right: auto;
}

