.description-link {
  width: 100px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $main-color;
  border: 0px solid transparent;
  border-radius: 10px;
  font-weight: 600;
  font-size: 16px;
  color: white;
  line-height: 100%;
  padding-top: 4px;
  margin: 2px;

  &:hover {
    background-color: transparent;
    color: $main-color;
    border: 2px solid $main-color;
    margin: 0px;
    transition: background-color .2s;
  }
}

.project-image-box {
  width: 420px;
}

.project-image {
  width: 100%;
  height: 100%;
  transition: transform .5s;

  &:hover {
    transform: scale(.95);
    transition: transform .5s;
  }
}

.bar {
  width: 100%;
  height: 2px;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #dbdbdb;
}

@media only screen and (max-width: 859px) {

  .project-image-box {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }

  .project-title {
    margin-top: 20px;
  }

  .project-description-box {
    width: 100%;
    max-width: 600px;
  }

  .description-link {
    padding-left: 10px;
    padding-right: 10px;
  }

  .description-link-wrapper {
    width: 100%;
    display: flex;
    justify-content: flex-start
  }

  #project-icon {
    margin-left: 5px;
  }

  .icon-container {
    justify-content: flex-start;
    padding-bottom: 0px;
  }

  .project-bottom-container {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .project-bottom {
    width: 100%;
  }
}

//Skills
.skill-wrap {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}

.skill-title {
  font-size: 25px;
  font-weight: 700;
  text-align: left;
  margin-left: 10px;
  margin-bottom: 10px;
}

.skill-container {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
}

.skill-icon-wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  flex-wrap: wrap;

}

.skill-icon {
  max-height: 75%;
  max-width: 75%;
  border-radius: 5px;
}

.skill-description {
  color: $main-color;
  position: absolute;
  font-weight: 700;
  font-size: 10px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 110%;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity .2s, transform .3s;
  user-select: none;
}

.skill-icon-container {
  height: 70px;
  width: 70px;
  background-color:$background-color2;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
  box-shadow: 0 0 2px transparent;
  transition: transform .5s;

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 4px #dbdbdb;
    transition: transform .5s, box-shadow .2s;


    .skill-icon {
      filter: brightness(.4);
      opacity: .2;
    }

    .skill-description {
      opacity: 1;
      transform: translateY(0px);
      transition: opacity .3s, transform .3s;
    }
  }

  @media only screen and (max-width: 350px) {
    height: 50px;
    width: 50px;
  }
}


//Certifications
#certifications {
 background-color: $background-color2
}

.certification {
  width: 90%;
  height: 120px;
  border: 1px solid #dbdbdb;
  background-color: #fcfcfc;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  transition: transform .2s, box-shadow .2s, border .2s, background-color .2s;

  &:hover {
    transform: scale(1.01);
    border: 1px solid transparent;
    box-shadow: 0 0 10px #dbdbdb;
    background-color: white;
    transition: transform .2s, box-shadow .2s, border .2s, background-color .2s;
  }
}

.certification-content {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: minmax(330px, 55%) auto 85px;;
  align-items: center;
}

.cert-img-wrap {
  height: 120px;
  width: 120px;
  padding-left: 10px;
  padding-right: 10px;

  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
}

.cert-img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
}

.certification-header-wrap {
  padding-left: 10px;
  padding-right: 10px;
}

.certification-header {
  font-weight: 700;
  font-size: 20px;
  padding-bottom: 10px;
}

.certification-subheader {
  font-weight: 200;
  font-size: 12px;
}

.certification-content-list {
  font-size: 10px;
  padding-left: 10px;
  text-align: left;
  padding-left: 20px;
}

.calendar-icon {
  width: 25px;
  height: 25px;
  margin-top: -8px;
}

.calendar-container {
  display: flex;
  align-items: center;
  margin-bottom: auto;
  margin-top: 15px;
  color: $color2;
  font-size: 18px;
  font-weight: 600;
  line-height: 0%;
}

.calendar-icon {
  margin-right: 5px;
  filter: invert(100%) brightness(.03) sepia(20) saturate(80) hue-rotate(540deg) brightness(3.5)
}

@media only screen and (max-width: 859px) {
  .certification-content-list {
    display: none;
  }

  .certification-content {
    grid-template-columns: minmax(330px, 90%) 85px;
  }
}

@media only screen and (max-width: 650px) {
  .certification {
    width: 100%;
    align-content: center;
  }

  .certification-header {
    font-size: 16px;
  }

  .mybg-subheader {
    padding: 5px;
    padding-top: 50px;
  }

  .certification-subheader {
    font-size: 10px;
  }

  .calendar-container {
    display: none;
  }
  
  .certification-content {
    grid-template-columns: 100%;
    height: 100%;
  }
}


//Contact
#contact {
  padding-top: 100px;
  min-height: 100vh;
  height: 100%;
  width: 100%;
//  background-color: white;
  display: inline-block;
  padding-bottom: 0;
}

.contact-form {
  width: 100%;
  height: 50vh;
  text-align: center;
  margin-bottom: auto;
}

.contact-el {
  width: 90%;
  min-width: 200px;
  max-width: 500px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 2px;
  border: none;
  box-shadow: 0 0 5px gray;
  font-family: 'Roboto', sans-serif;


  &:focus {
    outline: 2px solid $color2
  }
}

textarea {
  //height: 10vh;
  //flex-grow: 1;
  height: calc(70% - 100px);
  min-height: 25px;
  resize: none;
  font-family: 'Roboto', sans-serif;

  ::placeholder {
    font-family: 'Roboto', sans-serif
  }
}

.contact-button {
  margin-left: auto;
  margin-right: auto;
  width: 150px;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 10px;
  color: white;
  font-weight: 700;
  padding: 10px;

  &:hover {
    cursor: pointer;
    background-color: white;
    color: $main-color
  }
}

.skew-container-footer {
  position: relative;
  background: $background-color2;
  height: 100vh;
  overflow:hidden;
  display: none;
  z-index: 1;
  margin-bottom: -10px;
}

.skewed-footer {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, $main-color, $color2);
  z-index: -4;
  transform: skewY(-6deg);
  transform-origin: top left;
  margin-top: 15%;
  overflow:hidden;
}


//Footer 
#footer {
  width: 100%;
  height: 80px;
  color: white;
  padding-top: 15px;
  padding-bottom: 15px;
  box-shadow: 0 0 10px white;
  margin-top: auto;
  position: absolute;
  bottom: 0;
}

.footer-links {
  display: flex;
  justify-content: space-around;
  max-width: 300px;
  margin-left: auto;
  margin-right:auto;
}

.footer-link {
  margin-right: 20px;
  padding: 3px;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-content: center;
  border-bottom: 2px solid transparent;
  object-fit: cover;
  .footer-img {
    max-width: 90%;
    max-height: 90%;
    filter: invert(100%);
  }

  &:hover {
    border-bottom: 2px solid white;

    .footer-img {
      filter: invert(100%) brightness(.03) sepia(20) saturate(80) hue-rotate(540deg);
    }
  }
}

.copyright{
  text-align: center;
  margin-top: 10px;
}


@media screen and (max-width: 600px) {
  .skew-container {
    height: 70vh;
  }

  .mobile {
    display: block;
  }

  .title-fade {
    opacity: 1;
    $title-fade-duration: fadein .1s forwards;
    -webkit-animation: $title-fade-duration;
    -moz-animation: $title-fade-duration;
    -ms-animation: $title-fade-duration;
    animation: $title-fade-duration;
  }

  //My Background
  .mybg-list {
    padding-left: 0px;
    padding-right: 0px;
  }

  //Projects 
  .project-description {
    font-size: 14px;
    text-align: left;
  }

  #project-icon {
    width: 40px;
    height: 40px;
    margin-bottom: 10px;
  }

  .project-image-box {
    width: 100%;
  }

  .project-bottom-container {
    flex-wrap: wrap;
  }

  //Skills
  .skill-wrap {
    width: 100%;
    margin-left: auto;
    margin-right: auto;

  }

  .skill-icon-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  //Footer
  #footer {
    padding-top: 20px;
    padding-bottom: 30px;
  }
}
  
//transition 
.fade-in {
  opacity: 0;
  transition: opacity .5s;
}

.fade-in.appear {
  opacity: 1;
}

.fade-in-slow {
  opacity: 0;
  transition: opacity 1s;
}

.fade-in-slow.appear {
  opacity: 1;
}


@media screen and (max-width: 340px) {
  .page-title {
    font-size: 2.2em;
  }
}


